// common mixins - start

@mixin mkdfBckImageStyle() {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center;
}

@mixin mkdfImageOverlayHoverStyle($with-hover: true) {
	
	@if ($with-hover) {
		
		&:hover {
			
			&:after {
				opacity: 1;
			}
		}
		
		&:after {
			@include mkdfAbsoluteHolderLayout();
			content: '';
			background-color: rgba($default-background-color, .8);
			opacity: 0;
			transition: .3s;
		}
		
	} @else {
		@include mkdfAbsoluteHolderLayout();
		content: '';
		background-color: rgba($default-background-color, .8);
		opacity: 0;
		transition: .3s;
	}
}

@mixin mkdfStandardPaginationStyle($list_type: null) {
	@include mkdfRelativeHolderLayout(top);
	margin: 49px 0 68px;
	clear: both;
	
	ul {
		@include mkdfRelativeHolderLayout(top);
		padding: 0;
		margin: 0;
		list-style: none;
		text-align: center;
		
		li {
			position: relative;
			display: inline-block;
			vertical-align: top;
			margin: 0 21px;
			
			a {
				position: relative;
				display: inline-block;
				vertical-align: top;
				margin: 0;
				padding: 0;
				font-size: 18px;
				font-family: $default-heading-font;
			}
			
			&.mkdf-pag-active {
				
				a {
					color: $first-main-color;
				}
			}
			
			&.mkdf-pag-prev,
			&.mkdf-pag-next,
			&.mkdf-pag-first,
			&.mkdf-pag-last {
				margin: 0 2px;
				
				a {
					font-size: 32px;
					line-height: 22px;
					
					span {
						display: block;
						line-height: inherit;
						
						&:before {
							display: block;
							line-height: inherit;
						}
					}
				}
			}
			
			@if ($list_type == 'shortcode') {
				
				&.mkdf-pag-prev {
					
					a {
						opacity: 0;
					}
				}
				
				&.mkdf-pag-next {
					
					a {
						opacity: 1;
					}
				}
				
			} @else if ($list_type == 'shop') {
				span {
					position: relative;
					display: inline-block;
					vertical-align: top;
					margin: 0;
					padding: 0;
					color: $first-main-color;
					font-size: 18px;
					font-family: Montserrat,sans-serif;
				}
				
				a {
					
					&.next,
					&.prev {
						font-size: 0;
						line-height: 0;
						
						&:before {
							display: block;
							font-family: 'ElegantIcons'; // same icon pack as in our templates for pagination
							font-size: 32px;
							line-height: 22px;
							-webkit-font-smoothing: antialiased;
							-moz-osx-font-smoothing: grayscale;
						}
					}
					
					&.prev {
						margin-right: -10px;
						
						&:before {
							content: "\34";
						}
					}
					
					&.next {
						margin-left: -10px;
						
						&:before {
							content: "\35";
						}
					}
				}
			}
		}
	}
}

@mixin mkdfButtonDefaultStyle() {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	width: auto;
	margin: 0;
	font-family: $additional-button-font;
	font-size: 12px;
	line-height: 2em;
	letter-spacing: 0.250em;
	font-weight: 700;
	text-transform: uppercase;
	outline: none;
	box-sizing: border-box;
	transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out;
}

@mixin mkdfButtonSize($size: medium) {
	
	@if ($size == 'small') {
		padding: 11px 36px;
		
	} @else if ($size == 'medium') {
		padding: 15px 43px;
		
	} @else if ($size == 'large') {
		padding: 13px 66px;
		
	} @else if ($size == 'huge') {
		display: block;
		width: 100%;
		text-align: center;
		padding: 13px 26px;
	}
}

@mixin mkdfButtonTransparentColor() {
	color: $default-text-color;
	background-color: transparent;
}

@mixin mkdfButtonSolidColor($important: null) {
	color: $default-heading-color $important;
	background-color: #1d1d1d $important;
}

@mixin mkdfButtonSolidHoverColor($important: null) {
	color: $default-heading-color $important;
	background-color: #313131 $important;
}

@mixin mkdfButtonOutlineColor($important: null) {
	color: $first-main-color $important;
	background-color: transparent $important;
	border: 1px solid $first-main-color $important;
}

@mixin mkdfButtonOutlineHoverColor($important: null) {
	color: #fff $important;
	background-color: $first-main-color $important;
	border-color: $first-main-color $important;
}

@mixin mkdfPlaceholder {
	&::-webkit-input-placeholder {
		@content
	}
	
	&:-moz-placeholder {
		@content;
		
		opacity: 1;
	}
	&::-moz-placeholder {
		@content;
		
		opacity: 1;
	}
	&:-ms-input-placeholder {
		@content
	}
}

// common mixins - end

