// linear style - start

%linear-style {
	background-image: linear-gradient(148deg, #2e2e2e 5%, $additional-background-color 5%, $additional-background-color 50%, #2e2e2e 50%, #2e2e2e 55%, $additional-background-color 55%, $additional-background-color 100%);
	background-size: 18.87px 11.79px;
}

%linear-style-2 {
	background-image: linear-gradient(148deg, #0f0f0f 5%, $default-background-color 5%, $default-background-color 50%, #0f0f0f 50%, #0f0f0f 55%, $default-background-color 55%, $default-background-color 100%);
	background-size: 18.87px 11.79px;
}

// linear styles - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// mixins styles - start

%input-style {
	position: relative;
	width: 100%;
	margin: 0 0 #{$input-margin}px;
	padding: #{$input-vertical-padding}px #{$input-horizontal-padding}px;
	font-family: inherit;
	font-size: 15px;
	font-weight: inherit;
	line-height: calc(#{$input-height}px - (#{$input-vertical-padding * 2}px - 2px));
	color: $default-text-color;
	background-color: $additional-background-color;
	border: 0;
	border-radius: 0;
	outline: 0;
	cursor: pointer;
	-webkit-appearance: none;
	box-sizing: border-box;
	transition: border-color 0.2s ease-in-out;
	@extend %linear-style;

	footer & {
		@extend %linear-style-2;
	}
	
	&:focus {
		color: $default-heading-color;
		border-color: $default-text-color;
	}
	
	@include mkdfPlaceholder {
		color: inherit;
	}
}

// mixins styles - end

