// heading mixins - start

@mixin mkdfDefaultHeadingStyle() {
	@include mkdfHeadingStyle();
	margin: 25px 0;
	
	-ms-word-wrap: break-word;
	word-wrap: break-word;
	
	a {
		@include mkdfTypographyLayout();
		
		&:hover {
			color: $first-main-color;
		}
	}
}

@mixin mkdfHeadingStyle($with-heading: null, $with-color: true) {
	
	@if ($with-heading != 'h6') {
		font-family: $default-heading-font;
		text-transform: uppercase;
	}
	
	@if ($with-color) {
		color: $default-heading-color;
	}
	
	@if ($with-heading == 'h1') {
		@include mkdfH1();
	} @else if ($with-heading == 'h2') {
		@include mkdfH2();
	} @else if ($with-heading == 'h3') {
		@include mkdfH3();
	} @else if ($with-heading == 'h4') {
		@include mkdfH4();
	} @else if ($with-heading == 'h5') {
		@include mkdfH5();
	} @else if ($with-heading == 'h6') {
		@include mkdfH6();
	}
}

@mixin mkdfBody() {
	font-family: $default-text-font;
	font-size: 15px;
	font-weight: 400;
	line-height: 1.600em;
	color: $default-text-color;
	background-color: $default-background-color;
	-webkit-font-smoothing: antialiased;
}

@mixin mkdfH1() {
	font-size: 60px;
	line-height: 1.167em;
	font-weight: 300;
}

@mixin mkdfH2() {
	font-size: 48px;
	line-height: 1.167em;
    font-weight: 300;
}

@mixin mkdfH3() {
	font-size: 35px;
	line-height: 1.200em;
    font-weight: 300;
}

@mixin mkdfH4() {
	font-size: 23px;
	line-height: 1.304em;
	font-weight: 400;
}

@mixin mkdfH5() {
	font-size: 16px;
	line-height: 1.438em;
	font-weight: 400;
	letter-spacing: 1px;
}

@mixin mkdfH6() {
	font-size: 19px;
	line-height: 1.556em;
	font-family: $additional-text-font;
	color: $default-text-color;
	font-weight: 400;
	font-style: italic;
}

@mixin  mkdfSubTitle() {
	font-size: 23px;
	line-height: 1.174em;
	font-family: $additional-text-font;
	color: $additional-text-color;
	font-weight: 400;
	font-style: italic;
}

// heading mixins - end

