/* ==========================================================================
   WooCommerce Message Responsive styles - begin
   ========================================================================== */

@media only screen and (max-width: $ipad-portrait) {

    .mkdf-woocommerce-page {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 17px 25px;
        }
    }
}

@media only screen and (max-width: $phone-landscape) {

    .mkdf-woocommerce-page {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 14px 20px;
            line-height: 2em;

            a.wc-forward:not(.added_to_cart):not(.checkout-button) {
                top: 3px;
                float: left;
                margin: 0 75% 10px 0;
            }
        }
    }
}

@media only screen and (max-width: $phone-portrait) {

    .mkdf-woocommerce-page {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {

            a.wc-forward:not(.added_to_cart):not(.checkout-button) {
                margin: 0 62% 10px 0;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Message Responsive styles - end
   ========================================================================== */