/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - begin
   ========================================================================== */

@include laptop-landscape {
    
    .mkdf-woo-single-page {
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    
                    a {
                        padding: 13px 84px;
                    }
                }
            }
        }
    }
}

@include ipad-landscape {
    
    .mkdf-woo-single-page {
        
        .mkdf-single-product-content {
            
            .images,
            .mkdf-single-product-summary {
                float: none;
                width: 100%;
                padding: 0;
            }
	        
	        .images {
		
		        .woocommerce-product-gallery__trigger {
			        right: 10px;
		        }
	        }
            
            .mkdf-single-product-summary {
                margin: 43px 0 0;
            }
        }
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    
                    a {
                        padding: 13px 55px;
                    }
                }
            }

	        .entry-content {
			    padding: 0 50px;
		    }
        }
    }
}

@include ipad-portrait {

    .mkdf-woo-single-page {

        .woocommerce-tabs {

            ul.tabs {

                > li {

                    a {
                        padding: 13px 27px;
                    }
                }
            }

	        .entry-content {
			    padding: 0 35px;
		    }
        }
    }
}

@include phone-landscape {
    
    .mkdf-woo-single-page {

	    &.mkdf-woo-single-thumb-on-left-side {
		
		    .mkdf-single-product-content {
			
			    .images {
				
				    &.woocommerce-product-gallery--columns-4 {
					
					    .woocommerce-product-gallery__image {
						
						    &:not(:first-child) {
							    width: 50%;
						    }
					    }
				    }
			    }
		    }
	    }
        
        .mkdf-single-product-content {
            
            .images {
    
                &.woocommerce-product-gallery--columns-4 {
        
                    .woocommerce-product-gallery__image {
            
                        &:not(:first-child) {
	                        width: 50%;
                        }
                    }
                }
            }
        }

	    .woocommerce-tabs {

		    ul.tabs {
			    display: block;

			    li {
				    display: block;
				    width: 100%;

				    a {
					    display: block;
				    }
			    }
		    }

		    .entry-content {
			    padding: 0;
		    }
	    }
    }
}
/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - end
   ========================================================================== */